@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  color: #000000;
  min-width: 320px;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 30px;
    margin: 0 auto; } }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
    padding: 0 35px;
    width: auto; } }

@media (max-width: 767px) {
  .container {
    max-width: 767px;
    padding: 0 15px; } }

.main-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  position: absolute; }
  @media (max-width: 1023px) {
    .main-nav ul {
      flex-direction: column; } }

@media (max-width: 1023px) {
  .main-nav li {
    padding: 17px 40px 17px 16px; } }

.main-nav__item-link {
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  color: #ffffff;
  text-decoration: none;
  transition: 0.3s; }
  @media (max-width: 1023px) {
    .main-nav__item-link {
      font-size: 20px;
      line-height: 28px;
      font-weight: 400;
      color: #48494d;
      background-color: #f6f6f6;
      padding: 17px 40px 17px 16px; } }

.main-nav__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      display: block;
      position: absolute;
      width: 50px;
      height: 50px;
      background-color: #0ad9c6;
      border: none;
      top: 0;
      right: 0; }
      .main-nav__toggle::after {
        position: absolute;
        content: "";
        width: 30px;
        height: 30px;
        top: 10px;
        right: 10px;
        background-image: url("../img/menu-open.svg");
        background-repeat: no-repeat; } }

.promo {
  background-color: #000000;
  background-image: url("../img/bg-promo-desktop.jpg");
  background-repeat: no-repeat;
  min-height: 825px;
  background-size: cover; }

.promo__logo {
  display: block;
  transition: 0.3s; }
  .promo__logo:hover, .promo__logo:focus {
    opacity: 0.6; }
  .promo__logo:active {
    opacity: 0.3; }

.promo h1 {
  font-size: 46px;
  line-height: 64px;
  font-weight: 700;
  color: #ffffff;
  margin: 0; }
  @media (max-width: 1023px) {
    .promo h1 {
      font-size: 30px;
      line-height: 42px; } }

.promo__text-wrapper p {
  font-size: 20px;
  line-height: 32px;
  font-weight: 300;
  color: #ffffff;
  margin: 0; }
  @media (max-width: 1023px) {
    .promo__text-wrapper p {
      font-size: 18px;
      line-height: 32px; } }

.form p {
  font-size: 17px;
  line-height: 28px;
  font-weight: 600;
  color: #ffffff;
  text-align: center; }
  @media (max-width: 1023px) {
    .form p {
      font-size: 18px;
      line-height: 28px; } }

.form input {
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: #1c1c1c;
  border: 1px solid #c9ccd4;
  padding: 5px 30px;
  background-color: #ffffff;
  border-radius: 4px;
  transform: 0.3s; }
  .form input:hover, .form input:focus {
    color: #ffffff;
    border: 1px solid #0ad9c6;
    box-shadow: inset 0 0 0 1px #0ad9c6; }
  .form input:active {
    opacity: 0.5; }

.button {
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  color: #000000;
  background-color: #0ad9c6;
  padding: 10px 25px;
  text-align: center;
  border-radius: 6px;
  border: none;
  transition: 0.3s; }
  .button:hover, .button:focus {
    opacity: 0.8;
    color: #ffffff; }
  .button:active {
    opacity: 0.5; }

.page-header {
  position: relative; }
